document.addEventListener('DOMContentLoaded', function () {
  var btnsSidebar = document.querySelectorAll('.header__toggle'),
      layout = document.querySelector('.layout'),
      sidebar = document.querySelector('.sidebar'),
      mask = document.querySelector('.mask');

  for (var i = 0; i < btnsSidebar.length; i++) {
    btnsSidebar[i].addEventListener('click', function () {
      layout.classList.toggle('layout--sidebar-open'); // Управляем классом layout
      sidebar.classList.toggle('sidebar--show'); // Управляем классом sidebar
      mask.classList.toggle('mask--visible'); // Управляем маской
    });
  }

  mask.addEventListener('click', function () {
    layout.classList.remove('layout--sidebar-open'); // Закрываем меню
    sidebar.classList.remove('sidebar--show'); // Закрываем меню
    mask.classList.remove('mask--visible'); // Убираем маску
  });
});